.Ftrips {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
.titles {
  height: 40%;
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url("../assets/reels.webp") 0px -350px;
  background-size: cover;
}
.titles h1 {
  font-size: 5em;
  font-family: para;
  color: azure;
  margin-bottom: 3%;
  padding: 2% 0% 2% 2%;
  margin: 3% 5% 3% 2%;
  background-color: transparent;
  border-radius: 0.1em;
  text-decoration: underline;
}
.titles h3 {
  margin-top: 3%;
  font-size: 2em;
  font-family: para;
  color: azure;
  margin-bottom: 3%;
  padding: 2% 0% 2% 2%;
  margin: 3% 5% 3% 2%;
  background-color: transparent;
  border-radius: 0.1em;
}
.cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(32rem, 1fr));
  grid-template-rows: repeat(auto-fit, minmax(32rem, 1fr));
  justify-items: center;
  align-items: center;
  height: 80%;
  width: 100%;
  margin-bottom: 3%;
}
.details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  transform: translateX(43%);
  background-color: white;
  border-radius: 1em;
  margin: 1%;
  padding: 1%;
}
.details h2 {
  font-size: 2em;
  text-align: center;
  font-family: headers;
  color: azure;
  padding: 2%;
  margin: 2%;
  background-color: navy;
  border-radius: 1em;
}
.details p {
  margin-top: 3%;
  font-size: 1.4em;
  font-family: headers;
  color: azure;
  margin-bottom: 3%;
  padding: 3%;
  margin: 2%;
  background-color: navy;
  border-radius: 1em;
}
.details p:nth-child(even) {
  background-color: rgb(12, 12, 87);
}
.details span {
  font-weight: bold;
}
@media screen and (max-width: 666px) {
  .titles {
    height: 40%;
    background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
      url("../assets/reels.webp") 0px 0;
    background-size: cover;
    padding-bottom: 10%;
    margin-top: 10%;
  }
  .titles h1 {
    font-size: 2em;
    margin: 0% 5% 3% 2%;
  }
  .titles h3 {
    font-size: 1.5em;
  }
  .details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin: 0%;
    padding: 0%;
    transform: none;
  }
  .details h2 {
    font-size: 2em;
    text-align: center;
  }
  .details p {
    font-size: 1.4em;
    text-align: center;
  }
}

