.footer {
  height: 10%;
  background-color: white;
  margin: 0;
  padding: 0;
  position: relative;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.contact,
.logo2,
.links {
  height: 100%;
  width: calc(100% / 3);
  text-align: center;
}

.starida {
  height: 100%;
}

.links ul,
a {
  list-style: none;
  text-decoration: none;
}
@media screen and (max-width: 666px) {
  .starida {
    width: 100%;
  }
}

@media screen and (min-width: 667px) and (max-width: 735px) {
  .starida {
    width: 100%;
  }
}

@media screen and (min-width: 736px) and (max-width: 991px) {
  .starida {
    width: 100%;
  }
}

